@import url('https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap');

$dialog-color: #18191b;
$secundary-color: #111311;

html, body {

    user-select: none;
    outline: none;
}


.container {

    display: flex;
    justify-content: center;
    align-items: center;
    
    min-height: 95vh;
    outline: none;
    
    .card {
        
        background-color: $dialog-color;
        border-radius: 1vh;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;

        .title-section {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            background-color: $secundary-color;
            height: 5vh;
            border-top-left-radius: 1vh;
            border-top-right-radius: 1vh;

            .title {

                margin-left: 1.5vh;
                font-family: 'Yanone Kaffeesatz', sans-serif;
                font-weight: 400;
                font-size: 2vh;
                color: white;
            }
        }
        .center-section {

            display: flex;
            justify-content: flex-start;
            align-items: flex-start;

            flex-direction: column;

            margin: 1.5vh;
            padding: .5vh;
            border-radius: .3vh;
            background-color: $secundary-color;
            max-height: 30vh;
            overflow: auto;

            .listitem {
                color: white;
                font-family: 'Roboto Condensed', sans-serif;
                font-weight: 400;
                font-size: 1.9vh;
                margin-left: .5vh;
                margin: .1vh;
                line-height: 1.7;
                padding: 0 10vw 0 .5vw;
            }
        }
        .buttons {

            display: flex;
            justify-content: center;
            align-items: center;

            .btn {

                margin: .5vh 2vh 2vh 2vh;
                padding: .5vh 1vw .5vh 1vw;

                font-family: 'Roboto Condensed', sans-serif;
                font-weight: 600;
                font-size: 1.5vh;

                border-radius: .3vh;
                border: none;

                background-color: #ffffff;
                outline: none;
                transition: 0.5s;

                &:hover {
                    background-color: #d8d8d8;
                }
            }
        }
    }
}
::-webkit-scrollbar {
    display: none;
}
.active {

    padding: 0vh 10vw 0 .5vw !important;
    line-height: 1.7;
    border-radius: .5vh;
    background: linear-gradient(90deg, #2c2c2c 50%, #121212 90%);
}